import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
	name: 'cu-picker',
	props: {
		title: {
			type: String,
			default: 'null'
		},
		datas: {
			type: Array,
			default: () => {
				return []
			}
		}
	},
	components: {
		uniPopup
	},
	data() {
		return {
			visible: true,
			indicatorStyle: `height: ${Math.round(uni.getSystemInfoSync().screenWidth / (750 / 100))}px;`
		}
	},
	methods: {
		open() {
			this.$refs.popup.open()
		},
		close() {
			this.$refs.popup.close()
		},
		handleSubmit() {
			this.$emit('submit')
			this.close()
		},
		handleCancel() {
			this.$emit('cancel')
			this.close()
		},
		handleChange(item){
			this.$emit('change',item)
		}
	}
};
